import React from "react"
import ReactDOM from "react-dom"
import {DatePicker} from 'antd'
import moment from 'moment';
import 'moment/locale/zh-cn';
moment.locale('zh-cn');
import './css/index.css'
import Nebulas from "nebulas"
import NebPay from "nebpay.js";
const Neb = Nebulas.Neb;
const Account = Nebulas.Account;
const neb = new Neb();
const dappAddress = "n1rz5XxJ88F6btDaSRrmkisXpvHmBXQy23B";
//const dappAddress = "n1oqaMgqahSPxSSTawWnLg4jBAWZ1MJbUPD";

neb.setRequest(new Nebulas.HttpRequest("https://mainnet.nebulas.io"));
//neb.setRequest(new Nebulas.HttpRequest("https://testnet.nebulas.io"));
var from = Account.NewAccount().getAddressString();

var value = "0";
var nonce = "0"
var gas_price = "1000000"
var gas_limit = "2000000"
var callFunction = "get";
var callArgs = ""; //in the form of ["args"]
var contract = {
    "function": callFunction,
    "args": callArgs
}

var nebPay = new NebPay();
var to = dappAddress;
var money = "0";
var callFunction = "save";
var serialNumber;
var callArgs2 = "";

function loadData(cb){
  let data = [];
  neb.api.call(from, dappAddress, value, nonce, gas_price, gas_limit, contract).then(function(resp) {
      data = JSON.parse(resp.result);
      cb(data)
  }).catch(function(err) {
      console.log("error:" + err.message)
  })
}



class InputBox extends React.Component {
  constructor(props){
    super(props);
    this.handleChange = this.handleChange.bind(this)
  }
  handleChange(e){
    this.props.onEventChange(e.target.value)
  }
  render(){
  return (
    <div className="input-box">
      <input
        type="text"
        placeholder={this.props.placeholder}
        value={this.props.seeEvent}
        onChange={this.handleChange}
       />
      </div>
  )
}
}

function SubmitBtn(props) {
  return (<div className="input-box">
    <input type="submit" className="submit-btn" value=""/>
  </div>)
}

function ListItem(props) {
  return (<li>
    <p className="event">{props.event}</p>
    <p className="author">
      <span>Author:</span>{props.author}</p>
  </li>)
}
